/* eslint-disable prettier/prettier */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/no-unescaped-entities */
/* eslint-disable no-unused-expressions */
/* eslint-disable import/no-unresolved */

import React, { useState } from "react";

// @mui material components
import Card from "@mui/material/Card";
import { useLocation, useNavigate } from "react-router-dom";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import MDButton from "components/MDButton";
// Algocraze Dashboard React components
import MDBox from "components/MDBox";
import { useQuery } from "react-query";
import ClipLoader from "react-spinners/ClipLoader";
import { BrokerStatusCheck } from "services/backend_api";
import Brokerrecord from "./brokerrecord";
import ConnectAccount from "./ConnectAccount";

function LinkAccount() {

  const { state } = useLocation();
  const [sdata, setData] = useState();

  const { data } = useQuery("BrokerStatusCheck", () => {
    BrokerStatusCheck(state.uid).then((res) => {
      if (res) {
        // console.log("Res : ", res);
        setData(res);
        return res;
      }
      return false;
    });
  });
  data === undefined ? (
    <>
      <ClipLoader size={150} />
    </>
  ) : null;

  const navigate = useNavigate();

  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox pt={3}>
          <MDBox mt={-3} pt={3} pb={2} px={2}>
            <MDButton
              variant="gradient"
              color="info"
              circular
              iconOnly
              style={{ borderRadius: 100 }}
              onClick={() => navigate("/userbroker")}
            >
              <ArrowBackIosIcon fontSize="small" style={{ position: "absolute", right: 8.5 }} />
            </MDButton>
          </MDBox>
        </MDBox>
        {
          // eslint-disable-next-line
          // console.log("data", sdata),
          sdata === undefined ? (
            <div />
          ) : (
            <MDBox mt={3} mb={2}>
              <Card mt={5}>
                {sdata.broker_integrated === true ? (
                  <Brokerrecord
                    broker_id={sdata.broker_id}
                    broker_name={sdata.broker_name}
                    uid={state.uid}
                  />
                ) : (
                  <ConnectAccount uid={state.uid} />
                )}
              </Card>
            </MDBox>
          )
        }
      </DashboardLayout>
    </>
  );
}
export default LinkAccount;
