/* eslint-disable eqeqeq */
/* eslint-disable dot-notation */
// /* eslint-disable prettier/prettier */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

import MDButton from "components/MDButton";

import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import DataTable from "examples/Tables/DataTable";
import { useEffect, useState } from "react";
import { useMutation, useQuery } from "react-query";
import { AgentLiveTrading, BtnLiveTrading } from "services/backend_api";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import IsLoad from "services/IsLoad";
import swal from "sweetalert2";
import { useNavigate } from "react-router-dom";

function LiveTrading() {
  const sessionId = localStorage.getItem("agent_session_id");
  const navigate = useNavigate();
  useEffect(() => {
    if (!sessionId) {
      navigate("/");
    }
  }, []);

  const Toast = swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", swal.stopTimer);
      toast.addEventListener("mouseleave", swal.resumeTimer);
    },
  });

  const [sdata, setData] = useState([]);

  const [open, setOpen] = useState(false);
  const [val, setValues] = useState();
  const [com, setComment] = useState();
  const [livetradingbtn, setlivetradingbtn] = useState("off");

  const {
    data: alldata,
    refetch,
    isLoading,
  } = useQuery("AgentLiveTrading", () =>
    AgentLiveTrading().then((res) => {
      if (res) {
        // console.log("result ", res)
        setData(res);
        return res;
      }
      return false;
    })
  );

  const mutation = useMutation("BtnLiveTrading", (values) =>
    BtnLiveTrading(values.uid, values.status)
      .then((res) => {
        //  console.log("RES : ",res);
        if (res == true) {
          if (livetradingbtn === "on") {
            Toast.fire({
              icon: "success",
              title: "Live Trading Is ON",
            });
          } else {
            Toast.fire({
              icon: "success",
              title: "Live Trading Is OFF",
            });
          }
          refetch();
          setData(...[alldata]);
        } else if (res.error) {
          Toast.fire({
            icon: "error",
            title: `${res.error}`,
          });
        }
        return res;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const handleClick = (values) => {
    setValues(values);
    // console.log("v",values)
    if (values.status === "off_by_admin" || values.status === "off") {
      setComment("OFF");
    } else {
      setComment("ON");
    }
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    mutation.mutate(val);
    setOpen(false);
  };

  return (
    <MDBox pt={6} pb={3}>
      <Grid container spacing={6}>
        <Grid item xs={12}>
          <Card>
            <Grid item xs={12}>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Live Trading
                </MDTypography>
              </MDBox>
            </Grid>
            <MDBox>
              {isLoading ? (
                <IsLoad isLoad={isLoading} />
              ) : (
                <DataTable
                  table={{
                    columns: [
                      { Header: "id", accessor: "id", align: "center" },
                      { Header: "name", accessor: "name", align: "center" },
                      { Header: "mobile no", accessor: "mobile_no", align: "center" },
                      {
                        Header: "last updated time ",
                        accessor: "last_updated_time",
                        align: "center",
                      },
                      { Header: "status", accessor: "status", align: "center" },
                      { Header: "action", accessor: "action", align: "center" },
                    ],
                    rows: sdata.map((value, ind) => {
                      // eslint-disable-next-line prefer-const
                      let x = {};
                      const t = value["live_trading"];
                      const id = value["user_id"];

                      x["id"] = 1 + ind;
                      x["name"] = value["name"];
                      x["mobile_no"] = value["mobile_no"];
                      x["last_updated_time"] = value["last_updated_time"];
                      let liviTradingVal = "";
                      if (
                        value["live_trading"] === "off_by_admin" ||
                        value["live_trading"] === "off"
                      ) {
                        liviTradingVal = "off";
                      } else {
                        liviTradingVal = "on";
                      }
                      x["status"] = liviTradingVal;
                      x["action"] = (
                        <MDBox ml={-1}>
                          <MDButton
                            variant="gradient"
                            color={t === "on" ? "error" : "success"}
                            size="small"
                            onClick={() => {
                              setlivetradingbtn(t === "on" ? "off" : "on");
                              handleClick({ uid: id, status: t === "on" ? "off_by_admin" : "on" });
                            }}
                            fullWidth
                          >
                            {t === "on" ? "OFF" : "ON"}
                          </MDButton>
                        </MDBox>
                      );

                      return x;
                    }),
                  }}
                />
              )}
              <Dialog
                open={open}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle id="alert-dialog-title">Information</DialogTitle>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    Do you want to turn {com} live trading ?
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <MDButton onClick={handleClose}>No</MDButton>
                  <MDButton onClick={handleOpen} autoFocus>
                    Yes
                  </MDButton>
                </DialogActions>
              </Dialog>
            </MDBox>
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );
}
export default LiveTrading;
