/** 
  All of the routes for the Algocraze Dashboard React are added here,
  You can add a new route, customize the routes and delete the routes here.

  Once you add a new route on this file it will be visible automatically on
  the Sidenav.

  For adding a new route you can follow the existing routes in the routes array.
  1. The `type` key with the `collapse` value is used for a route.
  2. The `type` key with the `title` value is used for a title inside the Sidenav. 
  3. The `type` key with the `divider` value is used for a divider between Sidenav items.
  4. The `name` key is used for the name of the route on the Sidenav.
  5. The `key` key is used for the key of the route (It will help you with the key prop inside a loop).
  6. The `icon` key is used for the icon of the route on the Sidenav, you have to add a node.
  7. The `collapse` key is used for making a collapsible item on the Sidenav that has other routes
  inside (nested routes), you need to pass the nested routes inside an array as a value for the `collapse` key.
  8. The `route` key is used to store the route location which is used for the react router.
  9. The `href` key is used to store the external links location.
  10. The `title` key is only for the item with the type of `title` and its used for the title text on the Sidenav.
  10. The `component` key is used to store the component of its route.
*/

// Algocraze Dashboard React layouts
import Dashboard from "layouts/dashboard";
import SignIn from "layouts/authentication/sign-in";
import AddUser from "layouts/add_user";
import UserBroker from "layouts/user_broker";
import LiveTradning from "layouts/live_trading";
import UserSubscription from "layouts/user_subscription";
import UserReport from "layouts/user_report";
import StrategyListTable from "layouts/user_subscription/components/StrategyListTable";
import BrokerIntegration from "layouts/user_broker/broker_integration/components";
import Reports from "layouts/user_report/reports";
import Error from "layouts/page_not_found";
import Logs from "layouts/activity_logs";

// @mui icons
import Icon from "@mui/material/Icon";
import ReportPDF from "layouts/user_report/reports/ReportPDF";
import ReportCSV from "layouts/user_report/reports/ReportCSV";
import UserActivityLogs from "layouts/user_report/user_log";
import { USERACTIVITY, LOGS } from "utils/constant";

const routes = [
  {
    type: "collapse",
    name: "Dashboard",
    key: "dashboard",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: "/dashboard",
    component: <Dashboard />,
  },
  {
    type: "collapse",
    name: "Add User",
    key: "add_user",
    icon: <Icon fontSize="small">personadd</Icon>,
    route: "/adduser",
    component: <AddUser />,
  },
  {
    type: "collapse",
    name: "User Broker",
    key: "userBroker",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: "/userbroker",
    component: <UserBroker />,
  },
  {
    type: "collapse",
    name: "Live Trading",
    key: "live_tradition",
    icon: <Icon fontSize="small">analytics</Icon>,
    route: "/livetrading",
    component: <LiveTradning />,
  },
  {
    type: "collapse",
    name: "User Subscription",
    key: "userSubscription",
    icon: <Icon fontSize="small">subscriptions</Icon>,
    route: "/usersubscription",
    component: <UserSubscription />,
  },
  {
    type: "collapse",
    name: "User Report",
    key: "userReport",
    icon: <Icon fontSize="small">summarize</Icon>,
    route: "/userreport",
    component: <UserReport />,
  },
  {
    type: "collapse",
    name: "Activity",
    key: "logs",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: LOGS,
    component: <Logs />,
  },
  // {
  //   key: "signin",
  //   route: "/authentication/sign-in",
  //   component: <SignIn />,
  // },
  {
    key: "/strategylist",
    route: "/strategylist",
    component: <StrategyListTable />,
  },
  {
    key: "/brokerintegration",
    route: "/brokerintegration",
    component: <BrokerIntegration />,
  },
  {
    key: "/reports",
    route: "/reports",
    component: <Reports />,
  },
  {
    key: "/reportCSV",
    route: "/reports/ReportCSV",
    component: <ReportCSV />,
  },
  {
    key: "/reportPDF",
    route: "/reports/ReportPDF",
    component: <ReportPDF />,
  },
  {
    key: "Default",
    route: "/",
    component: <SignIn />,
  },
  {
    key: "signin",
    route: "/signin",
    component: <SignIn />,
  },
  // {
  //   key: "agent",
  //   route: "/agent",
  //   component: <SignIn />,
  // },
  {
    key: "Error",
    route: "*",
    component: <Error />,
  },
  {
    key: "useractivity",
    route: USERACTIVITY,
    component: <UserActivityLogs />,
  },
];

export default routes;
