/* eslint-disable no-nested-ternary */
/* eslint-disable react/jsx-no-undef */
/* eslint-disable object-shorthand */
/* eslint-disable prefer-destructuring */
/* eslint-disable camelcase */
/* eslint-disable dot-notation */
/* eslint-disable prettier/prettier */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import MDTypography from "components/MDTypography";
import DataTable from "examples/Tables/DataTable";
import { useNavigate } from "react-router-dom";
import { Brokerinfo } from "services/backend_api";
import { useState } from "react";
import { useQuery } from "react-query";
import IsLoad from "services/IsLoad";
import MDAlert from "components/MDAlert";

function BrokerUserList() {
  const sessionId = localStorage.getItem("agent_session_id");

  const navigates = useNavigate();
  if (!sessionId) {
    navigates("/");
  }
  /*
    if(state.re==="y"){
        window.location.reload(true)
    } */
  const [sdata, setData] = useState([]);
  const { isLoading } = useQuery("Brokerinfo", () =>
    Brokerinfo()
      .then((res) => {
        if (res) {
          setData(res);
          return res;
        }
        return false;
      })
      .catch((e) => {
        <MDAlert color="error" dismissible>
          {e}
        </MDAlert>;
      })
  );

  /*
    const {state} = useLocation();

     const re = state.re
     console.log("rrrrrrrrrrrrrrrrrrrrrrrrrrrrrrr",re)

    if(re){
        refetch();
    }
    */

  const navigate = useNavigate();

  const routeChange = (b_id, b_name, b_integrated, u_id) => {
    const path = "/brokerintegration";
    navigate(path, { state: { bid: b_id, bname: b_name, bintegrated: b_integrated, uid: u_id } });
  };

  return (
    <MDBox pt={6} pb={3}>
      <Grid container spacing={6}>
        <Grid item xs={12}>
          <Card>
            <Grid item xs={12}>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Broker List
                </MDTypography>
              </MDBox>
            </Grid>
            <MDBox>
              {sdata ? (
                isLoading ? (
                  <IsLoad isLoad={isLoading} />
                ) : (
                  <DataTable
                    table={{
                      columns: [
                        { Header: "id", accessor: "id", align: "center" },
                        { Header: "name", accessor: "name", align: "center" },
                        { Header: "mobile no", accessor: "mobile_no", align: "center" },
                        { Header: "status", accessor: "status", align: "center" },
                        { Header: "action", accessor: "action", align: "center" },
                      ],
                      rows: sdata.map((value, ind) => {
                        // eslint-disable-next-line prefer-const
                        let x = {};
                        let u_id = value["user_id"];
                        let b_name = value["broker_name"];
                        let b_id = value["broker_id"];
                        let b_integrated = value["broker_integrated"];
                        // console.log(b_integrated,b_id)
                        let check;
                        if (b_integrated) {
                          check = "Integrated";
                        } else {
                          check = "Not Integrated";
                        }
                        x["id"] = 1 + ind;
                        x["name"] = value["name"];
                        x["mobile_no"] = value["mobile_no"];
                        x["status"] = check;
                        x["action"] = (
                          <MDBox ml={-1}>
                            <MDButton
                              variant="gradient"
                              color={b_integrated ? "secondary" : "success"}
                              size="small"
                              fullWidth
                              onClick={() => {
                                routeChange(b_id, b_name, b_integrated, u_id);
                              }}
                            >
                              show
                            </MDButton>
                          </MDBox>
                        );
                        // console.log(x);
                        return x;
                      }),
                    }}
                  />
                )
              ) : null}
            </MDBox>
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );
}
export default BrokerUserList;
