/* eslint-disable dot-notation */
// /* eslint-disable prettier/prettier */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";

import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import MDTypography from "components/MDTypography";
import DataTable from "examples/Tables/DataTable";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import { useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { useQuery } from "react-query";
import { SubscriptionInfo } from "services/backend_api";
import IsLoad from "services/IsLoad";

function getPageContent() {
  const { state } = useLocation();

  const [sdata, setData] = useState([]);

  const { isLoading } = useQuery("SubscriptionInfo", () =>
    SubscriptionInfo(state.uid).then((res) => {
      if (res) {
        setData(res);
        // console.log("ttttttt",res)
        return res;
      }
      return false;
    })
  );

  const navigate = useNavigate();

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox py={3}>
        <MDBox mt={-3} py={3} px={2}>
          <MDButton
            variant="gradient"
            color="info"
            circular
            iconOnly
            style={{ borderRadius: 100 }}
            onClick={() => navigate("/usersubscription")}
          >
            <ArrowBackIosIcon fontSize="small" style={{ position: "absolute", right: 8.5 }} />
          </MDButton>
        </MDBox>
      </MDBox>
      <MDBox pt={1} pb={3}>
        <Grid container spacing={6}>
          <Grid item xs={12}>
            <Card>
              <Grid item xs={12}>
                <MDBox
                  mx={2}
                  mt={-3}
                  py={3}
                  px={2}
                  variant="gradient"
                  bgColor="info"
                  borderRadius="lg"
                  coloredShadow="info"
                >
                  <MDTypography variant="h6" color="white">
                    Strategy List
                  </MDTypography>
                </MDBox>
              </Grid>
              <MDBox>
                {isLoading ? (
                  <IsLoad isLoad={isLoading} />
                ) : (
                  <DataTable
                    table={{
                      columns: [
                        { Header: "id", accessor: "id", align: "center" },
                        { Header: "algo name", accessor: "algoname", align: "center" },
                        { Header: "stock name", accessor: "script", align: "center" },
                        { Header: "subscription date & time", accessor: "date", align: "center" },
                        { Header: "lot size", accessor: "lot_size", align: "center" },
                      ],

                      rows: sdata.map((value, ind) => {
                        // eslint-disable-next-line prefer-const
                        let x = {};
                        x["id"] = 1 + ind;
                        x["algoname"] = value["algoname"];
                        x["script"] = value["script"];
                        x["date"] = value["date"];
                        x["lot_size"] = value["lot_size"];
                        return x;
                      }),
                    }}
                  />
                )}
              </MDBox>
            </Card>
          </Grid>
        </Grid>
      </MDBox>
    </DashboardLayout>
  );
}

function StrategyListTable() {
  const sessionId = localStorage.getItem("agent_session_id");
  const navigate = useNavigate();
  if (!sessionId) {
    useEffect(() => {
      if (!sessionId) {
        navigate("/");
      }
    }, []);
  } else {
    return getPageContent();
  }
  return <></>;
}
export default StrategyListTable;
