/* eslint-disable no-param-reassign */
/* eslint-disable prettier/prettier */
/* eslint-disable no-console */
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
// @mui material components
import Card from "@mui/material/Card";

import swal from "sweetalert2";

// Algocraze Dashboard React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import { Grid } from "@mui/material";
import MDInput from "components/MDInput";
import { Formik } from "formik";
import MDButton from "components/MDButton";
import { useMutation } from "react-query";
import { Register } from "services/backend_api";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";

function getPageContent() {
  const Toast = swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", swal.stopTimer);
      toast.addEventListener("mouseleave", swal.resumeTimer);
    },
  });

  const mutation = useMutation("Register", (values) =>
    Register(values.name, values.email, values.password, values.phone.toString(), "User")
      .then((res) => {
        // console.log("success111 : ", res);
        if (res) {
          Toast.fire({
            icon: "success",
            title: "User added successfully",
          });
        }
        return res;
      })
      .catch((e) => {
        // console.log(e)
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox mt={3} mb={2}>
          <Card mt={5}>
            <MDTypography variant="h3" mt={3} pl={4} pr={4} fontWeight="medium" textAlign="center">
              Register User
            </MDTypography>
            <Formik
              initialValues={{
                name: "",
                email: "",
                phone: "",
                password: "",
                passwordConfirmation: "",
              }}
              validate={(values) => {
                const errors = {};
                if (!values.email) {
                  errors.email = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
                  errors.email = (
                    <div style={{ color: "red", fontSize: "12px" }}>Invalid email address</div>
                  );
                }

                if (!values.name) {
                  errors.name = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.name.length > 50) {
                  errors.name = (
                    <div style={{ color: "red", fontSize: "12px" }}>
                      Must be 50 characters or less
                    </div>
                  );
                }

                if (!values.phone) {
                  errors.phone = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.phone.toString().length > 10) {
                  errors.phone = (
                    <div style={{ color: "red", fontSize: "12px" }}>Phone number is not valid</div>
                  );
                } else if (values.phone.toString().length < 10) {
                  errors.phone = (
                    <div style={{ color: "red", fontSize: "12px" }}>Phone number is not valid</div>
                  );
                }

                if (!values.password) {
                  errors.password = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.password.length < 5) {
                  errors.password = (
                    <div style={{ color: "red", fontSize: "12px" }}>
                      Must be more than 5 Character
                    </div>
                  );
                } else if (values.password !== values.passwordConfirmation) {
                  errors.passwordConfirmation = (
                    <div style={{ color: "red", fontSize: "12px" }}>Password not matched</div>
                  );
                }
                return errors;
              }}
              onSubmit={(values, { resetForm }) => {
                mutation.mutate(values);
                resetForm({ values: "" });
              }}
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                resetForm,
                /* and other goodies */
              }) => (
                <MDBox sx={{ width: "100%" }}>
                  <Grid container mt={5} pl={4} pr={4} display="flex">
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="text"
                          label="Name"
                          name="name"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.name}
                          fullWidth
                        />
                        {errors.name && touched.name && errors.name}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="number"
                          label="Phone No"
                          name="phone"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.phone}
                          fullWidth
                        />
                        {errors.phone && touched.phone && errors.phone}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="email"
                          label="Email"
                          name="email"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.email}
                          fullWidth
                        />
                        {errors.email && touched.email && errors.email}
                      </MDBox>
                    </Grid>
                  </Grid>
                  <Grid container mt={1} pl={4} pr={4} display="flex">
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="password"
                          label="Password"
                          name="password"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.password}
                          fullWidth
                        />
                        {errors.password && touched.password && errors.password}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="password"
                          label="Confirm password"
                          name="passwordConfirmation"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.passwordConfirmation}
                          fullWidth
                        />
                        {errors.passwordConfirmation &&
                          touched.passwordConfirmation &&
                          errors.passwordConfirmation}
                      </MDBox>
                    </Grid>
                  </Grid>
                  <Grid
                    container
                    mt={1}
                    mb={4}
                    pl={4}
                    pr={4}
                    className="btnContainer"
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                  >
                    <MDBox pr={3}>
                      <MDButton
                        variant="gradient"
                        type="submit"
                        color="info"
                        onClick={() => {
                          handleSubmit();
                        }}
                        fullWidth
                      >
                        Add User
                      </MDButton>
                    </MDBox>
                    <MDBox pl={3}>
                      <MDButton
                        type="reset"
                        variant="gradient"
                        color="error"
                        onClick={resetForm}
                        fullWidth
                      >
                        Clear
                      </MDButton>
                    </MDBox>
                  </Grid>
                </MDBox>
              )}
            </Formik>
          </Card>
        </MDBox>
      </DashboardLayout>
    </>
  );
}
function addUser() {
  const sessionId = localStorage.getItem("agent_session_id");
  const navigate = useNavigate();
  if (!sessionId) {
    useEffect(() => {
      if (!sessionId) {
        navigate("/");
      }
    }, []);
  } else {
    return getPageContent();
  }
  return <></>;
}

export default addUser;
