/* eslint-disable no-nested-ternary */
/* eslint-disable dot-notation */
/* eslint-disable camelcase */
/* eslint-disable prettier/prettier */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import MDTypography from "components/MDTypography";
import DataTable from "examples/Tables/DataTable";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { useQuery } from "react-query";
import IsLoad from "services/IsLoad";
import { reportUserList } from "services/backend_api";
import { USERACTIVITY } from "utils/constant";

function UserReportList() {
  const navigate = useNavigate();
  const useractivityRouteChange = (uid) => {
    const path = USERACTIVITY;
    navigate(path, { state: { uid } });
  };
  const routeChange = (u_id) => {
    const path = `/reports`;
    navigate(path, { state: { uid: u_id } });
  };

  const [sdata, setData] = useState([]);
  const { isLoading } = useQuery("reportUserList", () =>
    reportUserList().then((res) => {
      if (res) {
        // console.log("res : ",res)
        setData(res);
        return res;
      }
      return false;
    })
  );

  return (
    <MDBox pt={6} pb={3}>
      <Grid container spacing={6}>
        <Grid item xs={12}>
          <Card>
            <Grid item xs={12}>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  User List
                </MDTypography>
              </MDBox>
            </Grid>
            <MDBox>
              {sdata ? (
                isLoading ? (
                  <IsLoad isLoad={isLoading} />
                ) : (
                  <DataTable
                    table={{
                      columns: [
                        { Header: "ID", accessor: "id", align: "center" },
                        { Header: "Name", accessor: "name", align: "center" },
                        { Header: "Email", accessor: "email", align: "center" },
                        { Header: "Total P&L", accessor: "totalpnl", align: "center" },
                        { Header: "Total Order", accessor: "orderCount", align: "center" },
                        { Header: "Activity", accessor: "activity", align: "center" },
                        // { Header: "Phone No.", accessor: "phone_no", align: "center" },
                        { Header: "Action", accessor: "action", align: "center" },
                      ],
                      rows: sdata.map((value, ind) => {
                        // eslint-disable-next-line prefer-const
                        let x = {};
                        let u_id = value["user_id"];
                        x["id"] = 1 + ind;
                        x["name"] = value["name"];
                        // x["mobile_no"] = value["mobile_no"];
                        x["email"] = value["email"];
                        x["totalpnl"] = value["total_pandl"];
                        x["orderCount"] = value["order_count"];
                        x["activity"] = (
                          <MDButton
                            variant="gradient"
                            color="info"
                            size="small"
                            fullWidth
                            onClick={() => {
                              useractivityRouteChange(value["user_id"]);
                            }}
                          >
                            view
                          </MDButton>
                        );
                        x["action"] = (
                          <MDBox ml={-1}>
                            <MDButton
                              variant="gradient"
                              color="success"
                              size="small"
                              fullWidth
                              onClick={() => {
                                routeChange(u_id);
                              }}
                            >
                              show
                            </MDButton>
                          </MDBox>
                        );
                        // console.log(x);
                        return x;
                      }),
                    }}
                  />
                )
              ) : null}
            </MDBox>
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );
}
export default UserReportList;
