/* eslint-disable no-alert */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */
/* prettier-ignore */
/* eslint-disable import/no-unresolved */
/**
=========================================================
* Algocraze Dashboard React - v1.0.0
=========================================================
*/

import { useState } from "react";
import { useMutation } from "react-query";

// react-router-dom components
import { Link, useNavigate } from "react-router-dom";

// @mui material components
import Card from "@mui/material/Card";
import Switch from "@mui/material/Switch";

// Algocraze Dashboard React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDInput from "components/MDInput";
import MDButton from "components/MDButton";

// Authentication layout components
import BasicLayout from "layouts/authentication/components/BasicLayout";

// Images
import bgImage from "assets/images/bg-sign-in-basic.jpeg";
import { Formik } from "formik";
import ReCAPTCHA from "react-google-recaptcha";
import swal from "sweetalert2";

// Services
import { Authenticate } from "services/backend_api";

const remember = localStorage.getItem("remember");

function Basic() {
  const sessionId = localStorage.getItem("agent_session_id");
  const navigate = useNavigate();
  if (sessionId) {
    navigate("/dashboard");
  }

  const Toast = swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", swal.stopTimer);
      toast.addEventListener("mouseleave", swal.resumeTimer);
    },
  });

  const [captchaToken, setCaptchaToken] = useState("");
  function onChange(value) {
    // console.log(value)
    setCaptchaToken(value);
  }

  const [rememberMe, setRememberMe] = useState(false);
  const mutation = useMutation("Authenticate", (values) =>
    Authenticate(values.email, values.password, captchaToken, rememberMe, "Agent")
      .then((res) => {
        if (res.username !== "") {
          Toast.fire({
            icon: "success",
            title: " Logged in Successfully",
          });
          setTimeout(() => {
            navigate("/dashboard");
          }, 300);
        }
        return res;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: "Invalid ID & Password",
        });
      })
  );

  return (
    <BasicLayout image={bgImage}>
      <Card>
        <MDBox
          variant="gradient"
          bgColor="info"
          borderRadius="lg"
          coloredShadow="info"
          mx={2}
          mt={-3}
          p={2}
          mb={1}
          textAlign="center"
        >
          <MDTypography variant="h4" fontWeight="medium" color="white" mt={1}>
            Sign in
          </MDTypography>
        </MDBox>
        <MDBox pt={4} pb={3} px={3}>
          <Formik
            initialValues={{ email: "", password: "" }}
            validate={(values) => {
              const errors = {};
              if (!values.email) {
                errors.email = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
              } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
                errors.email = (
                  <div style={{ color: "red", fontSize: "12px" }}>Invalid email address</div>
                );
              }
              return errors;
            }}
            onSubmit={(values, { setSubmitting, resetForm }) => {
              mutation.mutate(values);
              resetForm({ values: "" });
              window.grecaptcha.reset();
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              resetForm,
              isSubmitting,
              /* and other goodies */
            }) => (
              <MDBox>
                <MDBox mb={2}>
                  <MDInput
                    type="email"
                    name="email"
                    label="Email"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                    fullWidth
                  />
                  {errors.email && touched.email && errors.email}
                </MDBox>
                <MDBox mb={2}>
                  <MDInput
                    type="password"
                    name="password"
                    label="Password"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                    fullWidth
                  />
                  {errors.password && touched.password && errors.password}
                </MDBox>
                <MDBox mt={2}>
                  <ReCAPTCHA
                    sitekey="6LdvNoseAAAAAE3siMskmy4X7ybzVhmJ68XdSzQn"
                    onChange={onChange}
                  />
                </MDBox>
                <MDBox mt={4} mb={1}>
                  <MDButton
                    variant="gradient"
                    color="info"
                    type="submit"
                    onClick={handleSubmit}
                    fullWidth
                  >
                    sign in
                  </MDButton>
                </MDBox>
              </MDBox>
            )}
          </Formik>
        </MDBox>
      </Card>
    </BasicLayout>
  );
}

export default Basic;
