/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */
import React, { useState } from "react";
import MDBox from "components/MDBox";
import Grid from "@mui/material/Grid";
import MDButton from "components/MDButton";
import Icon from "@mui/material/Icon";
import MDInput from "components/MDInput";
import { useMutation } from "react-query";
import { Formik } from "formik";
import swal from "sweetalert2";
import { agentUserBrokerIntigration } from "services/backend_api";

function Aliceblue(props) {
  const Toast = swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", swal.stopTimer);
      toast.addEventListener("mouseleave", swal.resumeTimer);
    },
  });
  const [status, setStatus] = useState(false);

  const mutation = useMutation("agentUserBrokerIntigration", (values) =>
    agentUserBrokerIntigration(
      "aliceblue",
      values.userid,
      values.password,
      values.twofa,
      "",
      values.apisecret,
      values.appid,
      values.appname,
      props.uid
    )
    .then((res) => {
      if (res.status == false) {
        Toast.fire({
          icon: "error",
          title: `${res.error}`,
        });
      }
      if (res.status == true) {
        Toast.fire({
          icon: "success",
          title: `Added Successfully`,
        });
        setStatus(res.status);
      }
      return res;
    }).catch(e => {
      Toast.fire({
        icon: "error",
        title: `${e}`,
      });
    }),
  );

  if (status) {
    // navigate("userbroker", {replace: true});
    window.location.reload(true);
  }
  return (
    <>
      <MDBox ml={2}>
        <Formik
          initialValues={{
            userid: "",
            password: "",
            twofa: "",
            appname: "",
            appid: "",
            apisecret: "",
          }}
          validate={(values) => {
            const errors = {};
            if (!values.userid) {
              errors.userid = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }
            if (!values.password) {
              errors.password = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }
            if (!values.twofa) {
              errors.twofa = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }
            if (!values.appname) {
              errors.appname = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }
            if (!values.appid) {
              errors.appid = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }
            if (!values.apisecret) {
              errors.apisecret = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
            }

            return errors;
          }}
          onSubmit={(values, { resetForm }) => {
            mutation.mutate(values);
            resetForm({values:''})
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            resetForm,
            /* and other goodies */
          }) => (
            <Grid
              container
              spacing={2}
              display="flex"
              justifyContent="space-between"
              aligitems="center"
            >
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="text"
                  name="userid"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.userid}
                  label="User Name"
                  sx={{ width: "90%" }}
                />
                {errors.userid && touched.userid && errors.userid}
              </Grid>
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="password"
                  name="password"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.password}
                  label="Password"
                  sx={{ width: "90%" }}
                />
                {errors.password && touched.password && errors.password}
              </Grid>
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="text"
                  name="twofa"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.twofa}
                  label="Two Factor Varification"
                  sx={{ width: "90%" }}
                />
                {errors.twofa && touched.twofa && errors.twofa}
              </Grid>
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="text"
                  name="appname"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.appname}
                  label="APP Name"
                  sx={{ width: "90%" }}
                />
                {errors.appname && touched.appname && errors.appname}
              </Grid>
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="text"
                  name="appid"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.appid}
                  label="APP ID"
                  sx={{ width: "90%" }}
                />
                {errors.appid && touched.appid && errors.appid}
              </Grid>
              <Grid item xs={12} sm={4} md={4} mb={2}>
                <MDInput
                  type="text"
                  name="apisecret"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.apisecret}
                  label="API Secret"
                  sx={{ width: "90%" }}
                />
                {errors.apisecret && touched.apisecret && errors.apisecret}
              </Grid>

              <Grid item xs={12} sm={4} md={4}>
                <MDButton
                  variant="gradient"
                  alignitems="center"
                  onClick={() => {
                    handleSubmit();
                  }}
                  color="dark"
                  sx={{ width: "70%" }}
                >
                  <Icon sx={{ fontWeight: "bold" }}>add</Icon>
                  &nbsp;add
                </MDButton>
              </Grid>
            </Grid>
          )}
        </Formik>
      </MDBox>
    </>
  );
}
export default Aliceblue;
