import React, { useState } from "react";
import MDBox from "components/MDBox";
import Grid from "@mui/material/Grid";
import MDButton from "components/MDButton";
import MDInput from "components/MDInput";
import Icon from "@mui/material/Icon";
import { useMutation } from "react-query";
import { Formik } from "formik";
import swal from "sweetalert2";

// Services
import { agentUserBrokerIntigration } from "services/backend_api";

function Angleone(props) {

  const Toast = swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", swal.stopTimer);
      toast.addEventListener("mouseleave", swal.resumeTimer);
    },
  });
  // const navigate = useNavigate();
  const [status, setStatus] = useState(false);
  const mutation = useMutation("agentUserBrokerIntigration", (values) =>
    agentUserBrokerIntigration(
      "angelone",
      values.userid,
      values.password,
      "",
      values.apikey,
      values.apisecret,
      "",
      "",
      props.uid
    )
      .then((res) => {
        if (res.status == false) {
          Toast.fire({
            icon: "error",
            title: `${res.error}`,
          });
        }
        if (res.status == true) {
          Toast.fire({
            icon: "success",
            title: `Added Successfully`,
          });
          setStatus(res.status);
        }
        return res;
      }).catch(e => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      }),
    );

  if (status) {
    // navigate("/userbroker",{replace: true});
    window.location.reload(true);
  }

  return (
    <MDBox ml={2} component="form" role="form">
      <Formik
        initialValues={{
          userid: "",
          password: "",
          apikey: "",
          apisecret: "",
        }}
        validate={(values) => {
          const errors = {};
          if (!values.userid) {
            errors.userid = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
          }
          if (!values.password) {
            errors.password = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
          }
          if (!values.apikey) {
            errors.apikey = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
          }
          if (!values.apisecret) {
            errors.apisecret = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
          }

          return errors;
        }}
        onSubmit={(values, { resetForm }) => {
          mutation.mutate(values);
          resetForm({values: ''})
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          resetForm,
          /* and other goodies */
        }) => (
          <Grid
            container
            spacing={2}
            display="flex"
            justifyContent="space-between"
            alignItems="center"
          >
            <Grid item xs={12} sm={3} md={3} mb={2}>
              <MDInput
                type="text"
                name="userid"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.userid}
                label="User Name"
                sx={{ width: "90%" }}
              />
              {errors.userid && touched.userid && errors.userid}
            </Grid>
            <Grid item xs={12} sm={3} md={3} mb={2}>
              <MDInput
                type="password"
                name="password"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
                label="Password"
                sx={{ width: "90%" }}
              />
              {errors.password && touched.password && errors.password}
            </Grid>
            <Grid item xs={12} sm={3} md={3} mb={2}>
              <MDInput
                type="text"
                name="apikey"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.apikey}
                label="API Key"
                sx={{ width: "90%" }}
              />
              {errors.apikey && touched.apikey && errors.apikey}
            </Grid>
            <Grid item xs={12} sm={3} md={3} mb={2}>
              <MDInput
                type="text"
                name="apisecret"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.apisecret}
                label="API Secret"
                sx={{ width: "90%" }}
              />
              {errors.apisecret && touched.apisecret && errors.apisecret}
            </Grid>
            <Grid item xs={12} sm={3} md={3} mt={2}>
              <MDButton
                variant="gradient"
                color="dark"
                onClick={() => {
                  handleSubmit();
                }}
                sx={{ width: "70%" }}
              >
                <Icon sx={{ fontWeight: "bold" }}>add</Icon>
                &nbsp;add
              </MDButton>
            </Grid>
          </Grid>
        )}
      </Formik>
    </MDBox>
  );
}
export default Angleone;
