/* eslint-disable dot-notation */
// /* eslint-disable prettier/prettier */
/* eslint-disable no-console */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import MDTypography from "components/MDTypography";
import DataTable from "examples/Tables/DataTable";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { useQuery } from "react-query";
import { SubscriptionUser } from "services/backend_api";
import IsLoad from "services/IsLoad";

function getPageContent() {
  const [sdata, setData] = useState([]);

  const { isLoading } = useQuery("SubscriptionUser", () =>
    SubscriptionUser().then((res) => {
      if (res) {
        setData(res);
        return res;
      }
      return false;
    })
  );

  const navigate = useNavigate();

  const routeChange = (id) => {
    const path = `/strategylist`;
    navigate(path, { state: { uid: id } });
  };

  return (
    <MDBox pt={6} pb={3}>
      <Grid container spacing={6}>
        <Grid item xs={12}>
          <Card>
            <Grid item xs={12}>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Subscribe User
                </MDTypography>
              </MDBox>
            </Grid>
            <MDBox>
              {isLoading ? (
                <IsLoad isLoad={isLoading} />
              ) : (
                <DataTable
                  table={{
                    columns: [
                      { Header: "id", accessor: "id", align: "center" },
                      { Header: "name", accessor: "name", align: "center" },
                      {
                        Header: "total no of subscription",
                        accessor: "subscription_count",
                        align: "center",
                      },
                      { Header: "strategy", accessor: "status", align: "center" },
                    ],
                    rows: sdata.map((value, ind) => {
                      // eslint-disable-next-line prefer-const
                      let x = {};

                      let id = value["user_id"];
                      x["id"] = 1 + ind;
                      x["name"] = value["name"];
                      x["subscription_count"] = value["subscription_count"];
                      x["status"] = (
                        <MDBox ml={-1}>
                          <MDButton
                            variant="gradient"
                            color="success"
                            size="small"
                            fullWidth
                            onClick={() => {
                              routeChange(id);
                            }}
                          >
                            show
                          </MDButton>
                        </MDBox>
                      );
                      // console.log(x);
                      return x;
                    }),
                  }}
                />
              )}
            </MDBox>
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );
}

function SubscriptionTable() {
  const sessionId = localStorage.getItem("agent_session_id");
  const navigate = useNavigate();
  if (!sessionId) {
    useEffect(() => {
      if (!sessionId) {
        navigate("/");
      }
    }, []);
  } else {
    return getPageContent();
  }
  return <></>;
}
export default SubscriptionTable;
